import { useContext } from 'react';
import { DeleteContext } from '../../../pages/print/Print';


const functionType = {
  'score-range': 'Khong im',
  top: 'Top im',
  'year-range': 'im cc nm',
  compete: 'T l chi cc nm',
  area: 'Trng trong khu vc',
  group: 'Trng tng ng',
  special: 'Lp chuyn',
};

export const DeleteMenu = () => {
    const { data, setData, showDelete, setShowDelete, setToastMessage } = useContext(DeleteContext);

    const handleHideDelete = (e) => {
        if (e.target === e.currentTarget) {
            setShowDelete({
                show: false,
                index: 0,
            });
        }
    }

    const handleDelete = () => {
      const newData = data.filter((item, i) => i !== showDelete.index);
      setData(newData);
      setShowDelete({
          show: false,
          index: 0,
      });
      
      setToastMessage({
          type: 'success',
          msg: 'Xo thnh cng',
      });

    }
    return (
        <div
            className="w-full h-full fixed bg-black bg-opacity-40 flex justify-center items-center z-[3]"
            onClick={handleHideDelete}
        >
            <div className="p-8 bg-container-color rounded-lg relative">
              <div className='text-center'>
                <h1 className="text-lg text-center font-semibold my-2">
                  Xo mc &quot;{functionType[data[showDelete.index].dataType]}&quot;
                </h1>
                <p className='text-sm text-gray-400'>Hnh ng ny khng th c hon tc.</p>
              </div>

              <div className='flex justify-center gap-4 mt-8'>
                <button className='w-[5rem] p-2 rounded-lg border-2 border-border-color text-gray-400 font-semibold' onClick={handleHideDelete}>Hy</button>
                <button className='w-[5rem] p-2 rounded-lg bg-red-500 text-white font-semibold' onClick={handleDelete}>Xo</button>
              </div>
            </div>
        </div>
    );
};
