import { useState, useContext } from 'react';
import { FilterContext } from '../../pages/suggest/Suggest';
import { normalSubjectsObj, specialSubjectsObj } from '../../utils/lists';

export const Range = ({ min, max, schoolType, wish }) => {
    const { filterData, setFilterData, setToastMessage } = useContext(FilterContext);

    const [start, setStart] = useState(min);
    const [end, setEnd] = useState(max);

    const handleStart = (e) => {
        setStart(e.target.value);
    };

    const handleEnd = (e) => {
        setEnd(e.target.value);
    };

    const handleData = () => {
        if (start > end) {
            setToastMessage({
                type: 'warning',
                msg: 'im bt u phi nh hn im kt thc',
            });
            return;
        }

        if (start < min || end > max) {
            setToastMessage({
                type: 'warning',
                msg: `im bt u v kt thc phi nm trong khong ${min} - ${max}`,
            });
            return;
        }

        const startValue =
            schoolType === 'Lp thng'
                ? ((start / 30) * 100).toFixed(2)
                : start;
        const endValue =
            schoolType === 'Lp thng' 
                ? ((end / 30) * 100).toFixed(2) 
                : end;
        const wishValue =
            schoolType === 'Lp thng'
                ? normalSubjectsObj[wish]
                : specialSubjectsObj[wish].replace('1', '%');

        setFilterData({
            ...filterData,
            startValue,
            endValue,
            wishValue,
        });
    };

    return (
        <div className="mt-[2rem] grid grid-cols-3 gap-2">
            <input
                className="bs-in-light p-2 bg-transparent rounded-lg text-center"
                type="number"
                value={start}
                min={min}
                max={max}
                placeholder="im bt u"
                onChange={handleStart}
            />
            <input
                className="bs-in-light p-2 bg-transparent rounded-lg text-center"
                type="number"
                value={end}
                min={min}
                max={max}
                placeholder="im kt thc"
                onChange={handleEnd}
            />
            <button
                className="font-semibold bg-teal-600 text-white p-2 rounded-lg shadow-md"
                onClick={handleData}
            >
                Lc
            </button>
        </div>
    );
};
