import { useState, useRef, useEffect, useContext } from 'react';
import { FunctionContext } from '../../../pages/print/Print';
import { AddContext } from '../../../pages/print/Print';

import {
    yearsList
} from '../../../utils/lists';
import { getAxiosCompete, handleDataCompete } from '../../visual/utils';

export const Compete = () => {

    const { data, setData } = useContext(FunctionContext);
    const { showAdd, setShowAdd, setToastMessage } = useContext(AddContext);

    const mode = showAdd.mode
    const dataIndex = showAdd.index;

    const titleRef = useRef(null);
    const schoolRef = useRef(null);
    const startRef = useRef(null);
    const endRef = useRef(null);

    const [title, setTitle] = useState(mode === 'add' ? '' : data[dataIndex].title);
    const [school, setSchool] = useState(mode === 'add' ? '' : data[dataIndex].school);
    const [start, setStart] = useState(mode === 'add' ? yearsList[yearsList.length - 1] : data[dataIndex].start);
    const [end, setEnd] = useState(mode === 'add' ? yearsList[0] : data[dataIndex].end);

    const handleTitle = (e) => {
        setTitle(e.target.value);
    }

    const handleSchool = (e) => {
        setSchool(e.target.value);
    }

    const handleStart = (e) => {
        setStart(e.target.value);
    };

    const handleEnd = (e) => {
        setEnd(e.target.value);
    };


    // Add the data to the main array
    const addData = () => {

        if (end > yearsList[0] || start < yearsList[yearsList.length - 1]) {
            setToastMessage({
                type: 'warning',
                msg: `Nm bt u v kt thc phi nm trong khong t ${yearsList[yearsList.length - 1]} n ${yearsList[0]}`,
            });
            return;
        }
        
        getAxiosCompete(school)
            .then((res) => handleDataCompete(res))
            .then((tableData) => {

                if (tableData.length === 0) {
                    setToastMessage({
                        type: 'error',
                        msg: 'Khng tm thy trng',
                    });
                    return;
                }

                setData([
                    ...data,
                    {
                        dataType: 'compete',
                        title,
                        school: tableData.TEN_TRUONG,
                        start,
                        end,
                        tableData,
                    }
                ])
            })
            .then(() => setShowAdd({
                show: false,
                mode: 'add',
                index: 0,
            }));
    }

    const editData = () => {
        if (end > yearsList[0] || start < yearsList[yearsList.length - 1]) {
            setToastMessage({
                type: 'warning',
                msg: `Nm bt u v kt thc phi nm trong khong t ${yearsList[yearsList.length - 1]} n ${yearsList[0]}`,
            });
            return;
        }

        getAxiosCompete(school)
            .then((res) => handleDataCompete(res))
            .then((tableData) => {
                if (tableData.length === 0) {
                    setToastMessage({
                        type: 'error',
                        msg: 'Khng tm thy trng',
                    });
                    return;
                }
                const newData = data
                newData[dataIndex] = {
                    dataType: 'compete',
                    title,
                    school: tableData.TEN_TRUONG,
                    start,
                    end,
                    tableData,
                }
                setData(newData)
            })
            .then(() => setShowAdd({
                show: false,
                mode: 'add',
                index: 0,
            }));
    }

    useEffect(() => {
        if (mode === 'edit') {
            titleRef.current.value = data[dataIndex].title;
            schoolRef.current.value = data[dataIndex].school;
            startRef.current.value = data[dataIndex].start;
            endRef.current.value = data[dataIndex].end;
        }
    }, [mode])

    return (
        <div>
            <section>
                <img src="" alt="demo" />
            </section>
            <section className='w-full px-[10%] block pt-8'>
                <input 
                    type="text" 
                    className="block my-2 w-full bs-in p-2 bg-transparent rounded-lg text-center" 
                    placeholder='Nhp tiu  mc...'
                    onChange={handleTitle}
                    ref={titleRef}
                />
                <div className='w-full border-b-2 border-border-color'></div>
            </section>
            <section className="mt-[2rem] w-full grid grid-cols-2 gap-2 px-[10%]">
                <h1 className='text-center font-semibold'>Tn trng</h1>
                <input
                    className="bs-in p-2 bg-transparent rounded-lg text-center"
                    type="text"
                    placeholder="Nhp tn trng"
                    onChange={handleSchool}
                    ref={schoolRef}
                />
                <h1 className='text-center font-semibold'>Nm bt u</h1>
                <input
                    className="bs-in p-2 bg-transparent rounded-lg text-center"
                    type="number"
                    min={start}
                    max={end}
                    placeholder="Nhp nm bt u"
                    onChange={handleStart}
                    ref={startRef}
                />
                <h1 className='text-center font-semibold'>Nm kt thc</h1>
                <input
                    className="bs-in p-2 bg-transparent rounded-lg text-center"
                    type="number"
                    min={start}
                    max={end}
                    placeholder="Nhp nm kt thc"
                    onChange={handleEnd}
                    ref={endRef}
                />
            </section>
            {showAdd.mode === 'add' ? (
                <button className='float-right mt-[2rem] bg-teal-600 text-white p-2 rounded-lg' onClick={addData}>Thm mi</button>
            ) : (
                <button className='float-right mt-[2rem] bg-teal-600 text-white p-2 rounded-lg' onClick={editData}>Thay i</button>
            )}        
        </div>
    );
};
